
#app{
    text-align: center;
    margin-top: 20px; 
    font-family: Iosevka;
}
#logo{
    height: 200px;
      border-radius: 20px;
}
#center:hover {
   color: #F28585;
}
#left:hover {
   color: #F28585;
}
#right:hover {
   color: #F28585;
}
#center{
    color: black;
    margin-left: 20px;
    margin-right: 20px; 
    border-left: 5px solid #F28585;
    background-color: lightgrey;
}
#right{
    color: black;
      border-left: 5px solid #F28585;
  background-color: lightgrey;
}
#left{
    color: black;
      border-left: 5px solid #F28585;
  background-color: lightgrey;
}
nav{
    margin-top: 10px;
    margin-bottom: 30px;
}
#bawah{
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    text-align: center;
}
@font-face {
    font-family:"Iosevka";
  src: url(static/fonts/iosevka-regular.f36d062.ttf) format("truetype");
}
  /*Vue Typer*/
.vue-typer {
  font-family: Iosevka;
  background-color: white;
}
.vue-typer .custom.char {
  color: black;
  background-color: white;
}
.vue-typer .custom.char.selected {
  background-color: #F28585 ;
}
.vue-typer .custom.caret {
  width: 6px;
  background-color: #F28585;
}
#name{
  margin-top: 10px;
}



/* PC Css */
